* {
  margin: 0;
  padding: 0;
}

main {
  height: 100%;
  width: 100%;
}
main .main-container {
  display: flex;
}
main .main-container .login-form-section {
  width: 35%;
}
main .main-container .login-form-section .logo h1 {
  font-size: 70px;
  margin-top: 50px;
  margin-left: 50px;
}
main .main-container .login-form-section .logo h1 span {
  margin-left: 5px;
}
main .main-container .login-form-section .login-box {
  border: 1px solid #d1d8dd;
  width: 365px;
  height: 400px;
  margin-top: 50px;
  margin-left: 100px;
  padding-right: 10px;
  padding-left: 10px;
  text-align: center;
}
main .main-container .login-form-section .login-box .form-header {
  display: flex;
  margin-top: 30px;
}
main .main-container .login-form-section .login-box .form-header .dot {
  height: 8px;
  width: 8px;
  background-color: #5e64ff;
  border-radius: 50%;
  display: inline-block;
  margin-top: 4px;
  margin-right: 7px;
  margin-left: 150px;
}
main .main-container .login-form-section .login-box .form-header p {
  font-weight: bold;
}
main .main-container .login-form-section .login-box hr {
  border: 1px solid #d1d8dd;
  margin-top: 20px;
}
main .main-container .login-form-section .login-box .login-content {
  display: flex;
  flex-direction: column;
}
main .main-container .login-form-section .login-box .login-content #login_email, main .main-container .login-form-section .login-box .login-content #password {
  margin-top: 25px;
  margin-left: 30px;
  height: 28px;
  width: 300px;
  padding: 5px;
  border-radius: 6px;
  outline: 0;
  border-width: 0 0 2px;
  border-color: #DB231C;
  font-size: 14px;
}
main .main-container .login-form-section .login-box .login-content #login_email:focus, main .main-container .login-form-section .login-box .login-content #password:focus {
  border: 3px solid #040505;
}
main .main-container .login-form-section .login-box .login-content #password {
  margin-top: 15px;
}
main .main-container .login-form-section .login-box .login-content .login-btn {
  height: 28px;
  width: 310px;
  margin-top: 30px;
  margin-left: 30px;
  background-color: #DB231C;
  border-width: 0;
  border-radius: 3px;
  color: #fff;
  font-size: 14px;
}
main .main-container .login-form-section .login-box .form-footer {
  display: flex;
  flex-direction: column;
}
main .main-container .login-form-section .login-box .form-footer a {
  color: #8da9ce;
  font-size: 14px;
  font-weight: bold;
  text-decoration: none;
  margin-top: 30px;
}
@media (min-width: 0px) {
  main .main-container .for-login .page-card, main .main-container .for-forgot .page-card, main .main-container .for-signup .page-card, main .main-container .for-email-login .page-card {
    padding-top: 45px;
    padding-bottom: 45px;
    background-color: #fff;
    box-shadow: var(--shadow-base);
  }
}

:root,
[data-theme=light] {
  --primary: #0e0d0d;
  --secondary: #8b0803;
  --secondary-color: var(--secondary);
  --primary-color: var(--primary);
  --btn-height: 28px;
  /* // Checkbox */
  --checkbox-right-margin: var(--margin-xs);
  --checkbox-size: 14px;
  --checkbox-focus-shadow: 0 0 0 2px var(--gray-300);
  --checkbox-gradient: linear-gradient(
    180deg,
    --secondary-color,
    -124.51%,
    var(--primary) 100%
  );
}